@import 'base/mixins';
@import 'fonts.scss';


body {
    background: #FFFFFF;

    &.burger-active {
        @include tablet {
            padding-top: 102px;
        }

        @include phone {
            padding-top: 102px;
        }
    }
}

html,
body {
    overflow-x: hidden;
}

.container {
    position: relative;

    max-width: 1262px;
    width: 100%;
    margin: 0 auto;

    @include laptop {
        max-width: 960px
    }

    @include tablet {
        max-width: 720px;
    }

    @include tablet-sm {
        max-width: 540px;
    }

    @include phone {
        max-width: 100%;
        padding: 0 22px;
    }
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;

    background: #D71300;
    border-radius: 51px;
    border: none;
    padding: 17px 125px;

    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 150.9%;
    text-align: center;
    color: #FFFFFF;

    cursor: pointer;

    @include phone {
        padding: 20px;
        width: 100%;
        font-size: 18px;
    }

    &_sec {
        display: block;
        width: 100%;
        max-width: 560px;
        padding: 17px 30px;
        background: linear-gradient(90deg, #FD746C 0%, #FF9068 100%);

        @include laptop {
            padding: 17px 80px;
        }


        @include phone {
            padding: 20px;
            width: 100%;
            font-size: 18px;
        }
    }

    &_small {
        padding: 17px 30px;

        font-family: 'Zona Pro';
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 119.9%;
        color: #FFFFFF;

        @include phone {
            width: 100%;
        }
    }
}

.btn-badge {
    display: inline-flex;
    justify-content: center;
    align-items: center;

    position: relative;

    padding: 20px;
    background: linear-gradient(103.7deg, #1261AB 36.97%, #65B1E9 94.43%);
    border: none;
    border-radius: 60px;

    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 91.4%;
    letter-spacing: -0.01em;
    color: #344463;

    @include phone {
        font-size: 14px;
        padding: 11px 13px;
        border: 1px solid #183946;
        background: transparent;
    }

    &:after {
        content: '';

        position: absolute;
        top: 1px;
        left: 1px;
        right: 1px;
        bottom: 1px;
        z-index: 1;

        border-radius: 60px;
        background: #fff;

        @include phone {
            content: none;
        }
    }

    span {
        position: relative;
        z-index: 2;

        font-family: inherit;
        font-style: inherit;
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        letter-spacing: inherit;
        color: inherit;
    }

    &_filled {
        background: #FFFFFF;
        border: 1px solid #FFFFFF;
        color: #FE7A6C;

        &:after {
            content: none;
        }
    }
}

.logo {
    display: block;
    width: max-content;

    &__img {
        display: block;
        width: 44px;

        @include tablet {
            width: 39px;
        }
    }
}

.title-main {
    font-family: 'Zona Pro';
    font-style: normal;
    font-weight: 700;
    font-size: 47px;
    line-height: 120.5%;
    letter-spacing: -0.01em;
    color: #344463;

    @include laptop {
        font-size: 38px;
    }

    @include tablet {
        // font-size: ;
    }

    @include phone {
        font-size: 23px;
    }

    .fl {
        display: inline-block;
        width: 50%;

        @include phone {
            display: none;
        }
    }

    &_big {
        font-size: 62px;

        @include laptop {
            font-size: 54px;
        }

        @include phone {
            font-size: 28px;
        }

        .colored {
            @include phone {
                background: linear-gradient(90deg, #FD746C 0%, #FF9068 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                background-clip: text;
                text-fill-color: transparent;
            }
        }
    }
}

.header {

    padding: 24px 0;

    @include tablet {
        padding: 29px 0;
    }

    @include phone {
        padding: 18px 0;
    }

    &.active {
        @include tablet {
            position: fixed;
            left: 0;
            top: 0;
            right: 0;
            z-index: 10;

            background: #fff;
        }
    }

    &__body {
        position: relative;

        display: flex;
        align-items: center;

        @include tablet {
            justify-content: center;
        }
    }

    &__nav {
        margin-left: 70px;

        @include tablet {
            position: fixed;
            z-index: 10;

            left: -30px;
            top: 100px;
            bottom: -80px;
            right: -30px;

            padding: 0 30px;
            padding-bottom: 80px;
            margin: 0;

            background: #fff;

            opacity: 0;
            pointer-events: none;
            transition: .3s opacity ease-in-out;
        }

        &.active {
            @include tablet {
                opacity: 1;
                pointer-events: all;
            }
        }
    }

    &__list {
        display: flex;
        align-items: center;

        padding: 0;
        margin: 0;
        height: 100%;

        @include tablet {
            flex-direction: column;
            padding-top: 179px;
        }
    }

    &__list-item {
        margin-right: 34px;
        list-style: none;

        @include tablet {
            margin-right: 0;
            margin-bottom: 30px;
        }

        &_mobile {
            display: none;

            @include tablet {
                display: block;
                margin-top: auto !important;
                margin-bottom: 100px !important;
            }
        }
    }

    &__list-link {
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 148.9%;
        color: #344463;

        text-decoration: none;

        transition: cubic-bezier(0.075, 0.82, 0.165, 1) .3s all;

        @include tablet {
            font-family: 'Zona Pro';
            font-style: normal;
            font-weight: 700;
            font-size: 23px;
            line-height: 148.9%;
            color: #344463;
        }

        &:hover {
            color: #D71300;
            text-decoration: none;
        }
    }

    &__burger {
        display: none;

        @include tablet {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            position: absolute;
            right: 0;
            top: 50%;

            width: 30px;
            height: 30px;

            transform: translateY(-50%);
        }

        &.active {
            span:nth-child(1) {
                @include tablet {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    margin: 0;
                    transform: translate(-50%, -50%) rotate(45deg);
                }
            }

            span:nth-child(2) {
                display: none;
            }

            span:nth-child(3) {
                @include tablet {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    margin: 0;
                    transform: translate(-50%, -50%) rotate(-45deg);
                }
            }
        }

        span {
            @include tablet {
                display: block;

                height: 2px;
                width: 20px;
                margin-bottom: 5px;
                border-radius: 2px;

                background: #3F3F3E;

                transition: cubic-bezier(0.075, 0.82, 0.165, 1) .3s all;

                &:last-child {
                    margin-bottom: 0;
                }
            }

            &:nth-child(2) {
                @include tablet {
                    width: 14px;
                    margin-right: 6px;
                }
            }
        }
    }
}

.hero {

    position: relative;

    &__body {
        padding-top: 178px;
        padding-bottom: 245px;
        max-width: 655px;

        @include tablet {
            padding-top: 0;
            padding-bottom: 50px;
        }
    }

    &__body-info {
        display: flex;
        align-items: center;

        @include phone {
            display: block;
        }
    }

    &__title {
        margin-bottom: 43px;
        font-size: 48px;

        br.desktop {
            display: inline-block;

            @include phone {
                display: none
            }
        }

        br.mobile {
            display: none;

            @include phone {
                display: inline-block
            }
        }

        @include laptop {
            font-size: 40px;
        }

        @include tablet {}

        @include phone {
            margin-bottom: 24px;
            font-size: 28px;
        }
    }

    &__body-text {
        margin-right: 35px;
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 118.4%;
        color: #344463;

        @include phone {
            margin-bottom: 24px;
            margin-right: 0;
        }
    }

    &__btn {
        flex-shrink: 0;
    }

    &__img {
        position: absolute;
        top: -134px;
        right: -259px;
        z-index: -1;

        display: block;

        width: 917px;

        @include laptop {
            width: 700px;
            top: -54px;
            right: -209px;

        }

        @include tablet {
            position: static;
            top: unset;
            right: unset;

            width: 100%;
        }

        @include phone {
            margin-top: -35px;
        }
    }
}

.about {

    padding-bottom: 57px;

    @include phone {
        padding-top: 44px;
    }

    &__badge {
        position: absolute;

        @include phone {
            position: relative;
            margin-bottom: 8px;
        }
    }

    .title-main {
        @include laptop {
            padding-top: 10px;
        }

        @include phone {
            padding-top: 0;
            line-height: 133%;
            max-width: 330px;
        }
    }
}

.what {

    padding: 50px 0 60px;

    @include phone {
        padding-top: 10px;
        padding-bottom: 51px;
    }

    &__body {
        position: relative;

        padding: 100px 80px 115px;
        background: linear-gradient(90deg, #FD746C 0%, #FF9068 100%);
        border-radius: 63px;

        @include laptop {
            padding: 90px 70px 100px;
        }

        @include phone {
            margin-left: -22px;
            margin-right: -22px;
            padding: 65px 0;
            padding-left: 22px;
            padding-right: 22px;

            border-radius: 45px;
        }
    }

    &__badge {
        position: absolute;

        @include phone {
            position: static;
            margin-bottom: 8px;
        }
    }

    .title-main {
        color: #fff;

        @include laptop {
            padding-top: 10px;
        }

        @include phone {
            padding-top: 0;
            line-height: 133%;
        }

        br.mobile {
            display: none;

            @include phone {
                display: inline-block;
            }
        }
    }
}

.glass {
    padding: 60px 0 215px;

    @include tablet {
        padding-top: 0;
        padding-bottom: 191px;
    }

    &__grid {
        display: grid;
        grid-template-columns: 1fr 1fr;

        @include tablet {
            grid-template-columns: 100%;
        }
    }

    &__img-wrapper {
        padding: 0 80px;

        @include laptop {
            padding: 0 50px;
        }

        @include tablet {
            padding: 0;
            margin-bottom: 16px;
        }
    }

    &__img {
        border-radius: 45px;
        display: block;
        width: 100%;
    }

    &__title {
        margin-bottom: 45px;

        @include tablet {
            margin-bottom: 16px;
        }

        br.mobile {
            display: none;

            @include tablet {
                display: inline-block;
            }
        }
    }

    &__text {
        min-width: 540px;
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        color: #344463;

        @include phone {
            font-size: 16px;
            min-width: auto;
        }

        br {
            @include phone {
                display: none;
            }
        }

        br.mobile {
            display: none;

            @include phone {
                display: inline-block;
            }
        }
    }
}

.more {

    background: #344463;
    border-radius: 45px;

    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 500px;
        column-gap: 60px;

        @include laptop {
            grid-template-columns: 1fr 400px;
        }

        @include phone {
            display: flex;
            flex-direction: column-reverse;
        }
    }

    &__body {
        margin-top: 120px;
        padding-bottom: 175px;

        @include laptop {
            margin-top: 90px;
            padding-bottom: 100px;
        }

        @include phone {
            margin-top: 16px;
            padding-bottom: 80px;
        }
    }

    &__title {
        margin-bottom: 63px;
        color: #fff;
        font-size: 38px;

        @include laptop {
            font-size: 35px;
        }

        @include phone {
            font-size: 23px;
            margin-bottom: 40px;
            line-height: 133%;
        }

        br.mobile {
            display: none;

            @include phone {
                display: inline-block;
            }
        }
    }

    &__glass {
        position: relative;
        margin-top: -15px;
        margin-bottom: 96px;

        @include laptop {
            margin-top: -25px;
        }

        @include phone {
            margin-top: -45px;
            margin-bottom: 0;
        }

        &:before {
            content: '';

            position: absolute;
            top: -15px;
            right: -25px;
            bottom: 80px;

            display: block;

            width: 100%;

            background: linear-gradient(90deg, #FD746C 0%, #FF9068 100%);
            border-radius: 45px;

            @include laptop {
                bottom: 120px;
            }

            @include phone {
                top: -15px;
                right: -15px;
                bottom: 63px;
            }
        }
    }

    &__img {
        position: relative;
        z-index: 2;
        display: block;
        max-width: 475px;
        border-radius: 45px;

        @include laptop {
            max-width: 400px;
        }

        @include phone {
            width: 100%;
        }
    }

    &__btn {}
}

.bonus {
    padding: 72px 0 0;

    overflow-x: hidden;

    &__title {

        .colored {
            display: none;

            @include phone {
                display: inline
            }
        }

        .desktop {
            display: inline;

            @include phone {
                display: none;
            }
        }

        br.mobile {
            display: none;

            @include phone {
                display: inline-block;
            }
        }
    }

    &__grid {
        display: flex;
        column-gap: 90px;

        margin-top: 32px;

        @include phone {
            flex-direction: column-reverse;
            margin-top: 141px;
        }
    }

    &__body {
        min-width: 620px;
        padding-top: 80px;
        padding-bottom: 156px;

        @include laptop {
            min-width: auto;
            max-width: 420px;

            padding-top: 60px;
            padding-bottom: 120px;
        }

        @include phone {
            padding-top: 40px;
            padding-bottom: 0;
            min-width: auto;
        }
    }

    &__body-title {
        margin-bottom: 32px;
        min-width: 620px;

        font-size: 37px;

        @include laptop {
            font-size: 33px;
            min-width: auto;
        }

        @include phone {
            margin-bottom: 24px;
            font-size: 23px;
            min-width: auto;
        }
    }

    &__body-text {
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 130.9%;
        color: #344463;

        @include phone {
            line-height: 150%;
        }

        br.mobile {
            display: none;

            @include phone {
                display: inline-block;
            }
        }
    }

    &__glass {
        position: relative;

        width: 100%;

        @include phone {
            min-height: 500px;
        }

        &:after {
            content: '';

            position: absolute;
            left: 60px;
            top: 0;
            bottom: 65px;
            z-index: 1;

            display: block;
            border-radius: 45px;
            width: 55vw;

            background: linear-gradient(90deg, #FD746C 0%, #FF9068 100%);

            @include phone {
                bottom: 0;
                right: -22px;
                left: -22px;
                width: auto;

            }
        }
    }

    &__img {
        position: absolute;
        bottom: 0;
        right: 97px;
        z-index: 2;

        display: block;
        width: 586px;

        @include laptop {
            right: 87px;
            width: 400px;
        }

        @include phone {
            width: 510px;
            right: unset;
            bottom: 52px;
            left: 50%;
            transform: translateX(calc(-50% - 114px));
        }
    }
}

.countdown {

    padding: 126px 0 134px;

    @include phone {
        padding: 69px 0 74px;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__text {

        margin-bottom: 36px;

        font-family: 'Zona Pro';
        font-style: normal;
        font-weight: 600;
        font-size: 25px;
        line-height: 130.9%;
        text-align: center;
        color: #344463;

        span {
            display: block;

            font-family: 'Zona Pro';
            font-style: normal;
            font-weight: 600;
            font-size: 25px;
            line-height: 130.9%;
            text-align: center;
            color: #344463;
        }

        [data-countdown="out"] {
            color: #D71300;
        }
    }
}

.footer {
    padding: 24px 0;
    background: #344463;

    @include phone {
        padding: 0 0 32px;
        background: transparent;
    }

    &__grid {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include phone {
            flex-direction: column-reverse;
            row-gap: 20px;
            max-width: 227px;
            margin: 0 auto;
            justify-content: center;
        }
    }

    &__item {
        flex-grow: 1;
        flex-basis: 33%;

        @include phone {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    &__text {
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150.9%;
        text-align: center;
        color: #FFFFFF;

        @include phone {
            font-family: 'Open Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 124.9%;
            text-align: center;
            color: #344463;
        }

    }

    &__link {
        text-decoration: none;
    }
}



.l-how {
    &__popup {
        z-index: 100;
        position: fixed;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 15px;
        overflow-x: auto;
        transition: .3s;
        opacity: 0;
        visibility: hidden;

        pointer-events: none;

        &.--show {
            opacity: 1;
            visibility: visible;

            pointer-events: all;
        }

        &-inner {
            z-index: 1;
            position: relative;
            width: 100%;
            max-width: 620px;
            padding: 40px 68px;
            background: #F4F7FE;
            border-radius: 38px;
        }

        &-title {
            font-weight: 700;
            font-size: 20px;
            line-height: 120%;
            margin-bottom: 24px;
            max-width: 360px;
            font-family: 'RFDewiExtended';
        }

        &-close {
            position: absolute;
            top: 40px;
            right: 40px;
            cursor: pointer;
            path {
                transition: .3s;
            }
            &:hover {
                path {
                    fill: #D71300;
                }
            }
        }

        &-input {
            margin-bottom: 16px;

            input {
                display: block;
                width: 100%;
                height: 56px;
                background: #FFFFFF;
                border-radius: 20px;
                border: 1px solid #fff;
                font-size: 16px;
                font-weight: 600;
                padding: 0 19px;
                font-family: 'RFDewiExtended';
                transition: .3s;

                &.--error {
                    border-color: #f00;
                }
            }
        }

        &-button {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            font-size: 16px;
            line-height: 1.4;
            font-weight: 600;
            padding: 19px;
            font-family: 'RFDewiExtended';
            color: #fff;
            background: #D71300;
            border-radius: 22px;
            border: none;
            cursor: pointer;
            transition: .3s;

            &:hover {
                background: darken(#D71300, 10%);
            }
        }

        &-desc {
            margin: 24px auto 0;
            max-width: 330px;
            font-size: 13px;
            line-height: 120.34%;
            color: #59687A;
            text-align: center;

            a {
                color: #59687A;
                text-decoration: underline;

                &:hover {
                    text-decoration: none;
                }
            }
        }

        &-bg {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba(52, 68, 99, 0.7);
        }
    }

}


@media screen and (max-width: 450px) {
    .l-how {
        &__popup {
            &-inner {
                padding: 32px 26px;
            }

            &-title {
                padding-right: 25px;
            }

            &-close {
                top: 32px;
                right: 32px;
            }

            &-desc {
                font-size: 13px;
                line-height: 16px;
            }
        }
    }
}